.glyphicon-save, .glyphicon-open, .glyphicon-saved, .glyphicon-export, .glyphicon-import {
  &:before { height: .4em; overflow: hidden; content: @fa-var-hdd-o; display: block; position: absolute; line-height: 0.05; top: .6em; }
  &:after { position: relative; font-size: 60%; top: -.5em; left: .25em; display: inline-block; }
}
.glyphicon-save                              { &:after { content: @fa-var-arrow-down; } }
.glyphicon-open                              { &:after { content: @fa-var-arrow-up; } }
.glyphicon-saved                             { &:after { content: @fa-var-check; } }
.glyphicon-import                            { &:after { content: @fa-var-arrow-down; .fa-icon-rotate(-45deg, -0.5); } }
.glyphicon-export                            { &:after { content: @fa-var-arrow-up; .fa-icon-rotate(45deg, 0.5); } }
.glyphicon-send                              { &:before { content: @fa-var-send; } }
.glyphicon-floppy-disk                       { &:before { content: @fa-var-floppy-o; } }
.glyphicon-floppy-saved, .glyphicon-floppy-remove, .glyphicon-floppy-save, .glyphicon-floppy-open {
  &:before { content: @fa-var-floppy-o; }
  &:after { position: absolute; bottom: .1em; right: .1em; font-size: 50%; text-shadow: 1px 1px 2px white; }
}
